import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { AiOutlineCaretRight } from "react-icons/ai";
import { BiBuildingHouse } from "react-icons/bi";
import { IoIosClose } from "react-icons/io";
import { MdFilterList, MdLabel, MdModeEdit, MdSave } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  loadEducationCategories,
  PushPopupAlert,
} from "../../../actions";
import { getAllRoom } from "../../../actions/location";
import {
  createSchool,
  deleteSchool,
  getSchools,
  updateSchool,
} from "../../../actions/schools";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import {
  MainItem,
  SideNavigation,
  SubItem,
  SubItemContainer,
} from "../../../components/Navigation/SideNavigation";
import Table from "../../../components/Table/Table";
import { education_category } from "../../../interfaces/education_category";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { toTwoDigit } from "../../../shared/dataTest";
import {
  DistrictInterface,
  RoomLocationInterface,
  SchoolsInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";

interface AppProps {
  auth: Auth;
  education_categories: education_category[] | null;
  allRoom: RoomLocationInterface[];
  schools: SchoolsInterface[];
  getSchools: Function;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  createSchool: Function;
  getAllRoom: Function;
  updateSchool: Function;
  deleteSchool: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

export const getSchoolById = (
  schools: SchoolsInterface[],
  school_id: string
): SchoolsInterface => {
  return schools.filter((school) => school._id === school_id)[0];
};

const Index: React.FC<AppProps> = ({
  auth,
  education_categories,
  allRoom,
  schools,
  getSchools,
  loadEducationCategories,
  getAllRoom,
  updateSchool,
  deleteSchool,
  PushPopupAlert,
}) => {
  const [thisEdication, setThisEdication] = useState<education_category | null>(
    null
  );
  const [edicationCategoryFilterStatus, setEdicationCategoryFilterStatus] =
    useState<boolean>(false);
  const [thisProvince, setThisProvince] =
    useState<RoomLocationInterface | null>(null);
  const [provinceFilterStatus, setProvinceFilterStatus] =
    useState<boolean>(false);
  const [thisDistrict, setThisDistrict] = useState<DistrictInterface | null>(
    null
  );
  const [districtFilterStatus, setDistrictFilterStatus] =
    useState<boolean>(false);
  const [district, setDistrict] = useState<DistrictInterface[]>([]);
  const [schoolList, setSchoolList] = useState<SchoolsInterface[]>(schools);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [modalState, setModalState] = useState<"" | "CREATE" | "UPDATE">("");
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<{ element: string; msg: string } | null>(
    null
  );

  const [schoolName, setSchoolName] = useState<string>("");
  const [thisSchool, setThisSchool] = useState<SchoolsInterface | null>(null);
  const [instituteId, setInstituteId] = useState<string>("");
  const [provinceId, setProvinceId] = useState<string>("");
  const [districtId, setDistrictId] = useState<string>("");

  const loadDistrict = async () => {
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      setDistrict(res.data);
    } catch (error) {
      console.error({ code_error: error });
    }
  };

  useEffect(() => {
    setLoading(true);
    if (education_categories === null || education_categories.length === 0) {
      loadEducationCategories(setLoading);
    } else if (!allRoom.length) {
      getAllRoom((status: boolean, msg: string) => {
        if (!status && msg.length > 0) console.log(msg);
      });
    } else if (!schools.length) {
      getSchools((status: boolean, msg: string) => {
        console.log(status, msg);
        setLoading(status);
        if (msg.length > 0) {
          console.error({ loadingCandidatesError: msg });
        }
      });
    } else if (!district.length) {
      loadDistrict();
    } else if (schools.length) {
      setSchoolList(schools);
      setLoading(false);
    }
  }, [
    allRoom.length,
    education_categories,
    getAllRoom,
    district,
    loadEducationCategories,
    getSchools,
    schools,
  ]);

  const tableData = (): JSX.Element[] => {
    if (!schoolList.length) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={5}>
            No School Found
          </td>
        </tr>,
      ];
    }

    return schoolList.map((school, index) => (
      <tr
        key={index + school._id}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">{school.school_name}</td>
        <td className="py-3 px-5 text-left">
          {education_categories &&
          education_categories.filter(
            (edication) =>
              edication.education_institute_id === school.education_category_id
          ).length > 0
            ? education_categories.filter(
                (edication) =>
                  edication.education_institute_id ===
                  school.education_category_id
              )[0].education_category_title
            : "No Institute"}
        </td>
        <td className="py-3 px-5 text-left">
          {district &&
          district.filter(
            (district) => district.district_id === school.district_id
          ).length > 0
            ? district.filter(
                (district) => district.district_id === school.district_id
              )[0].district_name
            : "No district"}
        </td>
        <td className="px-3">
          <div className="flex items-center gap-2 float-right">
            <button
              className={`bg-blue-600 text-white font-bold py-1 px-2 flex items-center rounded focus:outline-none ${
                loadingRequest ? "cursor-not-allowed" : null
              }`}
              title="Edit School"
              disabled={loadingRequest}
              onClick={() => {
                setModalState("UPDATE");
                setThisSchool(school);
                setSchoolName(school.school_name);
                setInstituteId(school.education_category_id);
                setDistrictId(school.district_id);
                setProvinceId(
                  allRoom.filter((item) =>
                    item.districts.find(
                      (d) => d.district_id === school.district_id
                    )
                  )[0].province_id
                );
              }}
            >
              <MdModeEdit className="w-4 h-4" />
              Edit
            </button>
            <button
              className={`bg-red-700 text-white font-bold py-1 px-2 flex items-center rounded focus:outline-none ${
                loadingRequest ? "cursor-not-allowed" : null
              }`}
              title="Delete"
              disabled={loadingRequest}
              onClick={() => handleDelete(school._id)}
            >
              <MdModeEdit className="w-4 h-4" />
              Delete
            </button>
          </div>
        </td>
      </tr>
    ));
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!schoolName.length) {
      return setError({
        element: "school_name",
        msg: "School name is required",
      });
    }
    if (!instituteId.length) {
      return setError({
        element: "edication_id",
        msg: "Edication Institute is required",
      });
    }

    if (!districtId.length) {
      return setError({
        element: "districtId_id",
        msg: "District is required",
      });
    }

    setLoadingRequest(true);
    try {
      let res;
      if (modalState === "CREATE") {
        res = await axios.post<{
          data: {
            status: boolean;
            school_name: string;
            district_id: string;
            education_category_id: string;
            shool_id: string;
          };
        }>(`${API.APPLICATION}/schools`, {
          school_name: schoolName,
          district_id: districtId,
          education_category_id: instituteId,
        });
        if (res.data) {
          console.log({ res });
          setSchoolList([
            ...schoolList,
            {
              _id: res.data.data.shool_id,
              status: res.data.data.status,
              school_name: res.data.data.school_name,
              district_id: res.data.data.district_id,
              education_category_id: res.data.data.education_category_id,
            },
          ]);
          createSchool({
            _id: res.data.data.shool_id,
            status: res.data.data.status,
            school_name: res.data.data.school_name,
            district_id: res.data.data.district_id,
            education_category_id: res.data.data.education_category_id,
          });
          PushPopupAlert({
            title: "New School created successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setSchoolName("");
          setDistrictId("");
          setInstituteId("");
          setProvinceId("");
        }
      } else if (thisSchool && modalState === "UPDATE") {
        res = await axios.patch<{ update: SchoolsInterface }>(
          `${API.APPLICATION}/schools`,
          {
            school_id: thisSchool._id,
            school_name: schoolName,
            district_id: districtId,
            education_category_id: instituteId,
          }
        );
        if (res.data) {
          console.log({ res });
          updateSchool({
            _id: thisSchool._id,
            status: true,
            school_name: schoolName,
            district_id: districtId,
            education_category_id: instituteId,
          });
          PushPopupAlert({
            title: "School updated successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setDistrictId("");
          setInstituteId("");
          setProvinceId("");
          setThisSchool(null);
          setModalState("");
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      setLoadingRequest(false);
    }
  };

  const handleDelete = async (schoolId: string) => {
    if (window.confirm("Are you sure do you want to delete this room?")) {
      setLoadingRequest(true);
      try {
        const res = await axios.delete<SchoolsInterface>(
          `${API.APPLICATION}/schools/${schoolId}`
        );

        if (res.data) {
          console.log({ res });
          deleteSchool(schoolId);
          PushPopupAlert({
            title: "School deleted successfully",
            theme: PopupAlertTheme.success,
          });
        }
      } catch (error) {
        console.log({ province_error: error });
        PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadingRequest(false);
    }
  };

  const filterSchools = (
    FILTER_TYPE: "INSTITUTE" | "DISTRICT",
    FILTER_VALUE: string,
    IGNORE_DISTRICT: boolean = false
  ) => {
    var newArray: SchoolsInterface[] = [];
    if (FILTER_TYPE === "INSTITUTE") {
      if (thisDistrict && !IGNORE_DISTRICT) {
        if (FILTER_VALUE === "ALL") {
          newArray = schools.filter(
            (edication) => edication.district_id === thisDistrict.district_id
          );
        } else {
          newArray = schools.filter(
            (edication) =>
              edication.education_category_id === FILTER_VALUE &&
              edication.district_id === thisDistrict.district_id
          );
        }
      } else {
        if (FILTER_VALUE === "ALL") {
          newArray = schools;
        } else {
          newArray = schools.filter(
            (edication) => edication.education_category_id === FILTER_VALUE
          );
        }
      }
    }
    if (FILTER_TYPE === "DISTRICT") {
      if (thisEdication) {
        if (FILTER_VALUE === "ALL") {
          newArray = schools.filter(
            (edication) =>
              edication.education_category_id ===
              thisEdication.education_institute_id
          );
        } else {
          newArray = schools.filter(
            (edication) =>
              edication.district_id === FILTER_VALUE &&
              edication.education_category_id ===
                thisEdication.education_institute_id
          );
        }
      } else {
        if (FILTER_VALUE === "ALL") {
          newArray = schools;
        } else {
          newArray = schools.filter(
            (edication) => edication.district_id === FILTER_VALUE
          );
        }
      }
    }
    setSchoolList(newArray);
  };

  const countSchool = (
    FILTER_TYPE: "DEFAULT" | "EDUCATION" | "DISTRICT" = "DEFAULT",
    FILTER_VALUE: string = "ALL"
  ): string => {
    let counts: number = 0;
    if (FILTER_TYPE === "DEFAULT") {
      counts = schoolList.length;
    } else if (FILTER_TYPE === "EDUCATION") {
      counts = schoolList.filter(
        (item) => item.education_category_id === FILTER_VALUE
      ).length;
    } else if (FILTER_TYPE === "DISTRICT") {
      counts = schoolList.filter(
        (item) => item.district_id === FILTER_VALUE
      ).length;
    }
    return toTwoDigit(counts);
  };

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading schools...
      </div>
    );
  }

  return (
    <Fragment>
      <SideNavigation>
        {loading ? (
          <section className="animate-pulse">
            <div className="bg-gray-400 font-bold mb-2 h-7 px-2 rounded-md cursor-wait"></div>
            <div className="bg-gray-400 font-bold mb-2 h-7 px-2 rounded-md cursor-wait"></div>
            <div className="bg-gray-400 font-bold mb-2 h-7 px-2 rounded-md cursor-wait"></div>
          </section>
        ) : (
          <Fragment>
            {/* EDUCATION CATEGORY sections */}
            <span className="font-semibold text-lg text-gray-500">
              Filter by Institute
            </span>
            <section
              className={`mt-2 ${loadingRequest && "cursor-pointer"}`}
              onClick={() =>
                !loadingRequest &&
                setEdicationCategoryFilterStatus(!edicationCategoryFilterStatus)
              }
            >
              <MainItem
                title={
                  !thisEdication
                    ? "Choose Education Institute"
                    : thisEdication.education_category_title
                }
                active={
                  edicationCategoryFilterStatus || thisEdication !== null
                    ? true
                    : false
                }
              />
            </section>
            {edicationCategoryFilterStatus &&
              education_categories &&
              education_categories.length > 0 && (
                <SubItemContainer>
                  {education_categories &&
                    education_categories.map((edication, index) => (
                      <section
                        key={edication.education_institute_id + index}
                        onClick={() =>
                          !loadingRequest &&
                          (setThisEdication(edication),
                          setEdicationCategoryFilterStatus(false),
                          filterSchools(
                            "INSTITUTE",
                            edication.education_institute_id
                          ))
                        }
                      >
                        <SubItem
                          title={edication.education_category_title}
                          icon={MdLabel}
                          active={
                            thisEdication
                              ? edication.education_institute_id ===
                                thisEdication.education_institute_id
                              : false
                          }
                        />
                      </section>
                    ))}
                </SubItemContainer>
              )}
            {/* end EDUCATION CATEGORY sections */}

            {/* province sections */}
            <section>
              <span className="font-semibold text-lg text-gray-500">
                Filter by District
              </span>
              <section
                className="mt-2"
                onClick={() =>
                  !loadingRequest &&
                  (setEdicationCategoryFilterStatus(false),
                  setProvinceFilterStatus(!provinceFilterStatus),
                  setDistrictFilterStatus(false))
                }
              >
                <MainItem
                  title={
                    !thisProvince
                      ? "Choose Province"
                      : thisProvince.province_name
                  }
                  active={
                    provinceFilterStatus || thisProvince !== null ? true : false
                  }
                />
              </section>
            </section>
            {provinceFilterStatus && allRoom && allRoom.length > 0 && (
              <SubItemContainer>
                {allRoom &&
                  allRoom.map((province, index) => (
                    <section
                      key={province.province_id + index}
                      onClick={() =>
                        !loadingRequest &&
                        (setProvinceFilterStatus(false),
                        setThisProvince(province),
                        setThisDistrict(null),
                        thisEdication &&
                          filterSchools(
                            "INSTITUTE",
                            thisEdication.education_institute_id,
                            true
                          ))
                      }
                    >
                      <SubItem
                        title={province.province_name}
                        icon={MdLabel}
                        active={
                          thisProvince
                            ? province.province_id === thisProvince.province_id
                            : false
                        }
                      />
                    </section>
                  ))}
              </SubItemContainer>
            )}
            {/* end province sections */}

            {/* DISTRICT sections */}
            {thisProvince && (
              <section
                className="mt-2"
                onClick={() =>
                  !loadingRequest &&
                  (setEdicationCategoryFilterStatus(false),
                  setProvinceFilterStatus(false),
                  setDistrictFilterStatus(!districtFilterStatus))
                }
              >
                <MainItem
                  title={
                    !thisDistrict
                      ? "Choose District"
                      : thisDistrict.district_name
                  }
                  active={
                    districtFilterStatus || thisDistrict !== null ? true : false
                  }
                />
              </section>
            )}
            {districtFilterStatus &&
              thisProvince &&
              thisProvince.districts.length > 0 && (
                <SubItemContainer>
                  {thisProvince &&
                    thisProvince.districts.map((district, index) => (
                      <section
                        key={district.district_id + index}
                        onClick={() =>
                          !loadingRequest &&
                          (setThisDistrict(district),
                          setDistrictFilterStatus(false),
                          filterSchools("DISTRICT", district.district_id))
                        }
                      >
                        <SubItem
                          title={district.district_name}
                          icon={MdLabel}
                          active={
                            thisDistrict
                              ? district.district_id ===
                                thisDistrict.district_id
                              : false
                          }
                        />
                      </section>
                    ))}
                </SubItemContainer>
              )}
            {/* end DISTRICT sections */}
          </Fragment>
        )}
      </SideNavigation>

      <div className="w-full my-5 px-1">
        {/* header title */}
        <div className="flex items-start justify-between">
          <div className="flex flex-row items-center">
            <BiBuildingHouse className="text-blue-500 w-28 h-28" />
            <div className="flex flex-col">
              <h1 className="text-3xl font-semibold">Schools Management</h1>
              <div className="flex flex-row items-center mx-2 md:gap-3 gap-2">
                <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">All School(s)</span>
                  <span className="text-gray-600 text-2xl font-bold">
                    {countSchool()}
                  </span>
                </div>
                {education_categories &&
                  education_categories.length > 0 &&
                  education_categories.map((education, index) => (
                    <div
                      className="flex flex-col mt-1"
                      key={index + education.education_institute_id}
                    >
                      <span className="text-gray-600 text-base">
                        {education.education_category_title}
                      </span>
                      <span className="text-gray-600 text-2xl font-bold">
                        {countSchool(
                          "EDUCATION",
                          education.education_institute_id
                        )}
                      </span>
                    </div>
                  ))}
                {/* {userTabs !== TabTypes.Blocked_Users && ( */}
                {/* <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">
                    Vaccant Position(s)
                  </span>
                  <span className="text-gray-600 text-2xl font-bold">
                    {labelCounts.activeUser}
                    07
                  </span>
                </div> */}
                {/* )}
                {/* <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">
                    Blocked User(s)
                  </span>
                  <span className="text-gray-600 text-2xl font-bold">
                    12
                  </span>
                </div> */}
              </div>
            </div>
          </div>
          {/* <div className="flex items-center gap-2 mt-2">
            <label className="font-semibold text-gray-600 py-2">
              Academic period
            </label>
            <select
              className="bg-white border border-gray-700 p-1 focus:outline-none focus:bg-gray-100"
              required
            >
              <option value="">2020 - 2021</option>
            </select>
          </div> */}
        </div>
        {/* end header title */}

        <div className="md:w-98 bg-white mx-auto md:p-2">
          <div className="flex items-start justify-between">
            <div>
              {thisEdication && (
                <div className="flex">
                  <div className="flex items-center gap-1 mx-1">
                    <MdFilterList className="text-gray-600" />
                    <span className="font-semibold text-gray-500">
                      Institute:
                    </span>
                    <span className="font-semibold text-blue-700">
                      {thisEdication.education_category_title}
                    </span>
                  </div>
                  <IoIosClose
                    className="cursor-pointer rounded-full hover:bg-red-500 hover:text-white"
                    onClick={() =>
                      !loading &&
                      (setThisEdication(null),
                      filterSchools("INSTITUTE", "ALL"))
                    }
                    title="Clean Education Institute filter"
                  />
                </div>
              )}
              {thisProvince && (
                <div className="flex">
                  <div className="flex items-center gap-1 mx-1">
                    <MdFilterList className="text-gray-600" />
                    <span className="font-semibold text-gray-500">
                      Location:
                    </span>
                    <span
                      className={`font-semibold ${
                        thisDistrict
                          ? "text-black cursor-pointer"
                          : "text-blue-700"
                      }`}
                    >
                      {thisProvince.province_name}
                    </span>
                    {thisDistrict && (
                      <div className="flex items-center">
                        <AiOutlineCaretRight />
                        <span className="text-blue-700 font-semibold">
                          {thisDistrict.district_name}
                        </span>
                      </div>
                    )}
                  </div>
                  <IoIosClose
                    className="cursor-pointer rounded-full hover:bg-red-500 hover:text-white"
                    onClick={() =>
                      !loading &&
                      (setThisDistrict(null),
                      setThisProvince(null),
                      filterSchools("DISTRICT", "ALL"))
                    }
                    title="Clean location filter"
                  />
                </div>
              )}
            </div>
            <button
              type="button"
              className="px-4 py-3 bg-blue-600 rounded text-white focus:outline-none transform active:scale-x-75 transition-transform flex"
              onClick={() => setModalState("CREATE")}
              disabled={loadingRequest}
            >
              <MdSave className="h-6 w-6" />
              <span className="ml-2">Add School</span>
            </button>
            {/* <DownloadPDF reportTitle="Testing PDF" /> */}
          </div>
          <Table
            columns={[
              { columnTitle: "#", alignText: "left", padding: "p-3" },
              { columnTitle: "School", alignText: "left", padding: "p-3" },
              { columnTitle: "Institute", alignText: "left", padding: "p-3" },
              { columnTitle: "District", alignText: "left", padding: "p-3" },
              { columnTitle: "", alignText: "left", padding: "p-3" },
            ]}
            rows={tableData}
            pagination={true}
            search={true}
            thColor="gray"
          />
        </div>
      </div>

      {/* modal control */}
      {modalState && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setModalState("");
            setThisSchool(null);
            setSchoolName("");
            setInstituteId("");
            setDistrictId("");
            setProvinceId("");
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="text-gray-600 font-bold text-3xl md:ml-4 inline-flex items-center gap-2">
              <BiBuildingHouse className="w-11 h-11 text-gray-500 hover:bg-gray-500 hover:text-gray-50 rounded-full p-0 foucs:outline-none" />
              {modalState === "CREATE" ? "CREATE " : "UPDATE "} SCHOOL
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="mx-8 mb-8">
            <form onSubmit={(e) => handleSubmit(e)}>
              <div className="py-4 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                <div className="flex flex-col">
                  <label>School name</label>
                  <input
                    type="text"
                    className={`text-left input-white w-20 inline-block border-2 ${
                      error && error.element === "school_name"
                        ? "border-red-700"
                        : "border-gray-500"
                    }`}
                    placeholder="School name"
                    value={schoolName}
                    onChange={(e) => {
                      setError(null);
                      setSchoolName(e.target.value);
                    }}
                    disabled={loadingRequest}
                  />
                  {error && error.element === "school_name" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>

                {/* INSTITUTES ================= */}
                <div className="flex flex-col">
                  <label>Institutes</label>
                  {/* block appearance-none w-full bg-grey-lighter border
                  border-grey-lighter text-grey-darker py-3 px-4 pr-8 rounded */}
                  <select
                    className={`text-left input-white w-full block border-2 py-3 px-4 pr-8 ${
                      error && error.element === "edication_id"
                        ? "border-red-700"
                        : "border-gray-500"
                    }`}
                    value={instituteId}
                    onChange={(e) => setInstituteId(e.target.value)}
                    disabled={loadingRequest}
                  >
                    <option value="" disabled>
                      Choose Insitute
                    </option>
                    {education_categories &&
                      education_categories.map((institute, index) => (
                        <option
                          value={institute.education_institute_id}
                          key={index + institute.education_institute_id}
                        >
                          {institute.education_category_title}
                        </option>
                      ))}
                  </select>
                  {error && error.element === "edication_id" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>

                {/* PROVINCE ================= */}
                <div className="flex flex-col">
                  <label>Province</label>
                  <select
                    className={`text-left input-white w-20 inline-block border-2 border-gray-500`}
                    value={provinceId}
                    onChange={(e) => setProvinceId(e.target.value)}
                    disabled={loadingRequest}
                  >
                    <option value="" disabled>
                      Choose province
                    </option>
                    {allRoom &&
                      allRoom.map((province, index) => (
                        <option
                          value={province.province_id}
                          key={index + province.province_id}
                        >
                          {province.province_name}
                        </option>
                      ))}
                  </select>
                </div>

                {/* DISTRICT ================= */}
                {provinceId && (
                  <div className="flex flex-col">
                    <label>District</label>
                    <select
                      className={`text-left input-white w-20 inline-block border-2 ${
                        error && error.element === "districtId_id"
                          ? "border-red-700"
                          : "border-gray-500"
                      }`}
                      value={districtId}
                      onChange={(e) => setDistrictId(e.target.value)}
                      disabled={loadingRequest}
                    >
                      <option value="" disabled>
                        Choose district
                      </option>
                      {allRoom &&
                      allRoom.filter(
                        (province) => province.province_id === provinceId
                      )[0].districts.length > 0 ? (
                        allRoom
                          .filter(
                            (province) => province.province_id === provinceId
                          )[0]
                          .districts.map((district, index) => (
                            <option
                              value={district.district_id}
                              key={index + district.district_id}
                            >
                              {district.district_name}
                            </option>
                          ))
                      ) : (
                        <option key={702} disabled={true}>
                          No District Found
                        </option>
                      )}
                    </select>
                    {error && error.element === "districtId_id" && (
                      <span className="text-red-700">{error.msg}</span>
                    )}
                  </div>
                )}
              </div>
              <div className="pt-4 flex justify-between">
                <button
                  onClick={() => {
                    setModalState("");
                    setThisSchool(null);
                    setSchoolName("");
                    setInstituteId("");
                    setDistrictId("");
                    setProvinceId("");
                  }}
                  className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none`}
                  type="button"
                  disabled={loadingRequest}
                >
                  Exit
                </button>
                <button
                  className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                    loadingRequest ? "animate-pulse" : null
                  }`}
                  type="submit"
                  disabled={loadingRequest}
                >
                  {loadingRequest ? "Loading..." : "Save"}
                </button>
              </div>
            </form>
          </div>
        </Modal>
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  schools,
  schoolLevels,
  locations,
}: StoreState): {
  auth: Auth;
  schools: SchoolsInterface[];
  education_categories: education_category[] | null;
  allRoom: RoomLocationInterface[];
} => {
  return {
    auth: auth,
    schools: schools.schools,
    education_categories: schoolLevels.education_categories,
    allRoom: locations.roomLocation,
  };
};

export const Schools = connect(mapStateToProps, {
  createSchool,
  getSchools,
  updateSchool,
  getAllRoom,
  deleteSchool,
  loadEducationCategories,
  PushPopupAlert,
})(Index);
